.page {
    background-color: #000000;
    margin: 0 auto;
    max-width: 1280px;
    min-width: 320px;
    min-height: 100%;
    font-family: 'Inter','Arial', 'Helvetica', sans-serif;
    -webkit-font-smoothing: antialiased;
